@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

html {
  box-sizing: border-box;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  background-color: rgb(240, 240, 240);
}

*,
*:after,
::before {
  box-sizing: inherit;
}

input[type="text"] {
  border: none;
  border-radius: 0.24rem;
  padding: 0.75rem;
  margin-bottom: 1rem;
  outline: none;
  margin-bottom: 1rem;
  outline: none;
  display: block;
  width: 100%;
  font-size: 1rem;
  line-height: 1;
  background-color: transparent;
}

button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
  border: thin solid rgb(114, 114, 114);
  border-radius: 0.25rem;
  -webkit-border-radius: 0.25rem;
  -moz-border-radius: 0.25rem;
  -ms-border-radius: 0.25rem;
  -o-border-radius: 0.25rem;
  padding: 0.5rem 1rem;
  margin: 0.0.5rem 0 0;
  display: inline-block;
  background-color: #fff;
  color: rgb(53, 53, 53);
  font-weight: bold;
  font-size: 1rem;
  line-height: 1;
  text-transform: none;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;

}

input[type='button']:disabled{
  border-color: rgba(118,118,118,.3);
  color: rgba(16,16,16,.3);
  background-color: rgba(240,240,240,.3);
  cursor: default;
}

form {
  margin-bottom: 1rem;
  margin: 0rem;
}

hr {
  margin: 2rem auto;
}
h3,h2{
  text-align: center;
}



img{
  max-width: 80%;
  height: auto;
  justify-content: center;
  margin: 0px 50px;
}

table {
  border-spacing: 0;
  border-collapse: 0.5rem;
  width: 100%;
}

th,
td {
  text-align: left;
  padding: 0.5rem;
  border-bottom: thin solid #fff;
}

@media screen and (min-width: 1024px) {
  .grid-1-2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: start;
    gap: 2rem;
  }

  .grid-1-3 {
    display: grid;
    grid-template-columns: 1fr 1fr ;
    align-items: start;
    gap: 1rem; 

  }
}